document.addEventListener('DOMContentLoaded', function(){
    let Contenedor = document.querySelector('.hero-contenido');
    Carrusel(Contenedor);
});

function Carrusel(contenedor){
    contenedor.addEventListener('click', function(e){
        let Atras = document.querySelector('.flecha-izquierda'),
            Adelante = document.querySelector('.flecha-derecha'),
            Hero = document.querySelector('.hero'),
            SubTitulo = document.querySelector('.ml6 .letters'),
            Tgt = e.target,
            Contador = 0
        ;
        
        if(Tgt === Adelante){
            Hero.style.backgroundImage = "url('../../build/img/etienne-beauregard-riverin-B0aCvAVSX8E-unsplash.png')";
            Adelante.classList.add('opacidad');
            Adelante.classList.remove('cursor');
            Atras.classList.remove('opacidad');
            Atras.classList.add('cursor');

            Contador++;
        }
        else if(Tgt === Atras){
            Hero.style.backgroundImage = "url('../../build/img/sean-pollock-PhYq704ffdA-unsplash.jpg')";
            Adelante.classList.remove('opacidad');
            Adelante.classList.add('cursor');
            Atras.classList.remove('cursor');
            Atras.classList.add('opacidad');
            Contador--;
        }
    });
}
